	module.exports = {"data":{"markdownRemark":{"html":"<p>黄色い看板のなんとやらにお世話になってとりあえず家賃は払ったけど、まだまだ災難は続く。今度は単車にて走行中にケータイ落としてなくしてしまう始末。仕事上、ケータイなきゃ話にならないので至急なくしたケータイの一時停止を申し込むと共に新規ケータイを契約。そこで新しい電話番号に感動。なんと11桁の番号の中に5が6つもあるしｗ  ペンタブレット初体験。Wacom intuos2が激安で売ってたので金欠だろうが迷わず購入。早速<a href=\"/3181\">お絵かき</a>してみたが、まだ修行の余地ありまくりな感じ。</p>","frontmatter":{"cover":null,"date":"2004-09-15T00:00:00+09:00","strDate":"2004/9/14","title":"厄年"}}},"pathContext":{"path":"/ja/blog/2004/09/15-厄年/","langKey":"ja"}}

